<script>
  import Header from "./widgets/Header.svelte";
  import Running from "./pages/Running.svelte";
  import Paused from "./pages/Paused.svelte";
  import Allowed from "./pages/Allowed.svelte";
  import Offline from "./pages/Offline.svelte";
  import { getTabInfo } from "../api/api.js";
  import { setValidatedMethodBrowser } from "../lib/validated-method.js";
  import * as browser from "webextension-polyfill";

  // API all other components use
  const browserMethods = {
    sendMessage: browser.runtime.sendMessage,
    addListener: browser.runtime.onMessage.addListener,
    getURL: browser.runtime.getURL,
    browserTabs: browser.tabs,
  };
  setValidatedMethodBrowser(browserMethods)

  // immediately grab the current tab info for the popup
  let tabId;
  if (
    document.location.search &&
    document.location.search.indexOf("tabId") > 0
  ) {
    const params = new URLSearchParams(document.location.search);
    tabId = params.get("tabId");
    if (tabId === "error") {
      // allows testing of the error handling logic
      throw new Error("anError");
    }
  }
  getTabInfo.call({tabId}).then(tabInfo => {
    if (tabInfo) {
      if (tabInfo.disabledSite) {
        state = 'offline'
      } else if (tabInfo.paused) {
        disabled = true
        state = 'paused'
      } else if (tabInfo.allowListed) {
        disabled = true
        state = 'allowed'
      } else {
        state = 'running'
      }
    }
  });

  // if paused or allowed listed - disable
  let disabled = false
  // state of the extension
  let state = ''
</script>

<Header disabled={disabled}/>
<section>
  {#if state === 'running'}
    <Running/>
  {:else if state === 'paused'}
    <Paused/>
  {:else if state === 'allowed'}
    <Allowed/>
  {:else if state === 'offline'}
    <Offline/>
  {/if}
</section>

<style>
    section {
        background: white;
        margin: 0;
        padding: 24px 18px;
        display: flex;
        flex-direction: column;
    }
</style>
