<script>
import pauseIcon from '../../media/pause.svg'
import { translator } from "../translator.js";
import {pause} from '../../api/api.js'

export let disabled = false

translator.addListener(t => translator.translateClass(t, "pause-text"))

function onPause() {
  pause.callAndReload()
}
</script>

<button id="pause" {disabled} on:click|preventDefault={onPause}>
  <img
    class="too-high"
    src={pauseIcon}
    alt="pause"
    height="15"
    width="12"
    aria-hidden="true"
  />
  <span class="pause-text"></span>
</button>

<style>
  button {
      background: #75140c;
      color: white;
  }

  button:disabled {
      color: #c6c6c6;
      background: #f4f4f4;
  }

  button:disabled span {
      color: #c6c6c6;
  }
</style>
